"use client";

import "./CommonEventDetails.css";

export default function CommonEventDetails({ event = {}, onBack }) {
  const {
    title,
    description,
    mode,
    bannerImage,
    tags = [],
    org = {},
    tickets = [],
  } = event;

  return (
    <div className="event-page">
      {/* üî• BACK BUTTON */}
      <button className="back-btn" onClick={onBack}>
        ‚Üê Back to Events
      </button>

      <div className="event-hero">
        <img src={bannerImage || "/images/event-placeholder.png"} alt={title} />
      </div>

      <h1>{title}</h1>
      <p>{description || "Details coming soon"}</p>

      <p>
        üìç {org.city}, {org.state}, {org.country}
      </p>

      <p>Mode: {mode}</p>

      <div className="ticket-box">
        <h3>Tickets</h3>
        {tickets.length ? (
          tickets.map((t, i) => (
            <div key={i}>
              {t.name} ‚Äì ‚Çπ{t.price}
            </div>
          ))
        ) : (
          <p>Tickets not announced</p>
        )}
      </div>
    </div>
  );
}
